import React from 'react'
import PropTypes from 'prop-types'
import { Link } from 'gatsby'
// import { GatsbyImage as Img } from 'gatsby-plugin-image'
// https://www.gatsbyjs.com/docs/reference/built-in-components/gatsby-plugin-image/
// https://www.gatsbyjs.com/docs/how-to/images-and-media/using-gatsby-plugin-image/
// GatsbyImage 有 cache bug, 抓不到 logoQuery 結果
import { StaticImage as Img } from 'gatsby-plugin-image'
import { Heading } from 'theme-ui'
// import { Container, Box, Flex } from 'theme-ui'

const styles = {
  image: {
    verticalAlign: `middle`,
    width: 64,
    height:64,
    marginRight: 5,
    borderRadius: '50%'
  },
  grayscale: {
    WebkitFilter: `grayscale(1)`,
    filter: `grayscale(1)`,
    opacity: `0.7`
  },
  title: {
    m: 0,
    display: 'inline', 
    boxSizing: 'content-box'
  }
}

const Logo = ({ title, grayscale, image, to, ...props }) => (
  <Heading
    as={Link}
    to={to}
    alt={title}
    aria-label={title}
    variant='h2'
    sx={styles.title}
    {...props}
  >
    {image ? (
      <Img
        // image={image}
        src="./logo.png"
        loading='eager'
        style={
          grayscale
            ? { ...styles.grayscale, ...styles.image }
            : { ...styles.image }
        }
        // {...props}
      />
    ) : (
      title
    )}
    <h4 style={{ color: 'gold', display: 'inline', boxSizing: 'content-box' }}>
      {title}
    </h4>
  </Heading>
)

export default Logo

Logo.defaultProps = {
  to: '/'
}

Logo.propTypes = {
  title: PropTypes.string,
  grayscale: PropTypes.bool,
  fixed: PropTypes.object, //gatsby-transform-sharp
  to: PropTypes.string
}
