import React from 'react'
import { Container, Box, Flex, Text } from 'theme-ui'
import { FooterLogo } from '@layout/Footer/Footer.Logo'
import { FooterMenu } from '@layout/Footer/Footer.Menu'
import { FooterSocial } from '@layout/Footer/Footer.Social'
import useSiteMetadata from '../../../helpers/useMySiteMetadata'
 
const styles = {
  wrapper: {
    position: `relative`,
    bg: `footerBg`,
    mt: [5, 6]
  }
}

export const Footer = () => {
  const { disclaimer } = useSiteMetadata()

  return (
    <Box sx={styles.wrapper}>
      <Container variant='compact' sx={styles.container}>
        <Flex variant='layout.footer'>
          <Box>
            <FooterLogo />
          </Box>
          <FooterMenu />
          <Box>
            <FooterSocial />
          </Box>
        </Flex>
        <Text
          sx={{
            textAlign: 'center'
          }}
        >
          免責聲明：{disclaimer}
        </Text>
        <Box>
          <br></br>
        </Box>
      </Container>
    </Box>
  )
}
