import React from 'react'
import { Box } from 'theme-ui'
// import PoweredByGatsby from '@components/PoweredByGatsby'
import { HeaderLogo } from '@layout/Header/Header.Logo'
import AppInfo from "@qiyo_components/AppInfo.js"

const styles = {
  logo: {
    pb: 1,
    mb: 2,
    mt: [4, 0]
  },
  copyright: {
    pt: 2,
    mb: [2, 4]
  }
}

export const FooterLogo = () => (
  <>
    <Box sx={styles.logo}>
      <HeaderLogo grayscale />
    </Box>
    <Box sx={styles.copyright}>
      © {new Date().getFullYear()}, {AppInfo.companyChineseName}版權所有.
    </Box>
    {/*
    <Box>
      <PoweredByGatsby />
    </Box>
    */}
  </>
)
